@import '../../style/css/breakpoints';

.landing-page {
  min-height: 100vh;
  display: flex;
  flex: 0 1 auto;
  flex-direction: column;
  justify-content: center;
  color:white;

  main {
    display: flex;
    flex: 1 0 auto;
    flex-direction: column;
    justify-content: center;
    padding: 3.25rem 1.5rem 1rem;
    text-align: center;

    @include breakpoint-small-down {
      padding: 4.25rem 1.5rem 1rem;
    }

    .intro-wrapper {
      flex: auto;
      display: flex;
      flex-direction: column;
      justify-content: center;
      text-align: center;

      > .intro-name {
        font-family: 'Reem Kufi', sans-serif;
        font-size: 4rem;
        font-weight: bold;
        line-height: 4rem;
        padding-bottom: 1rem;
        text-transform: uppercase;
        letter-spacing: 8px;

        @include breakpoint-small-down {
          font-size: 3rem;
          line-height: 3.5rem;
        }
      }

      > .tagline {
        font-size: 2.5rem;
        margin: 1.5rem 0;
        font-weight: 300;
        text-transform: uppercase;
        letter-spacing: 5px;

        @include breakpoint-small-down {
          font-size: 1.25rem;
        }
      }
    }

    .browser-notes {
      font-size: 0.75rem;

      span {
        border-width: 1px;
        border-style: solid;
        padding: 0.125rem 0.5rem;
      }
    }
  }
}


