  .dynamic-text {
    position: relative;
  }
  .dynamic-text:after {
    content: '|';
    color: white;
    margin-left: 10px;
    position: absolute;
    left: calc(100% - 10px);
  }
  .dynamic-text.fade:after {
    -webkit-animation: fade 1s infinite ease-in-out;
            animation: fade 1s infinite ease-in-out;
  }
  
  @-webkit-keyframes fade {
    0%, 100% {
      opacity: 1;
    }
    50% {
      opacity: 0;
    }
  }
  
  @keyframes fade {
    0%, 100% {
      opacity: 1;
    }
    50% {
      opacity: 0;
    }
