.skills-page {
    padding: 2rem;
    min-height: 100vh;
    display: flex;
    flex: 0 1 auto;
    flex-direction: column;
    justify-content: center;
    color:black;
}

.skills-item {
    margin: 10px;
}
.skills-page h1 {
    padding: 1rem 0;
    font-size: 3rem;
    text-transform: uppercase;
    letter-spacing: 8px;
}
.skills-page .content-grid {
    flex: auto;
}
.skills-page .skills-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

@import url(https://fonts.googleapis.com/css?family=Raleway);
* {
    margin: 0px;
    padding: 0;
}

.container {
    max-width: 800px;
    margin: auto;
}

#left {
    float: left;
}

#right {
    float: right;
}

body {
    background: #333;
    font-family: 'Raleway', sans-serif;
}

h1 {
    color: #fff;
    margin-left: 0px;
    margin-top: 15px;
    margin-bottom: 30px;
}

h2 {
    color: #fff;
    margin-left: 0px;
    margin-top: 15px;
    margin-bottom: 30px;
}

.skills-bar-container {
    position: relative;
    width: 40%;
    min-width: 350px;
    margin: 5% auto;
    list-style: none;
}

.skills-bar-container li {
    background: #666666;
    position: relative;
    width: 100%;
    height: 3px;
    margin-bottom: 35px;
    margin-top: 8px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
}

.skills-bar-container label {
    color: #fff;
    position: relative;
    margin-left: 0px;
    font-size: 16px;
    font-weight: 300;
}

.progressbar {
    position: absolute;
    width: 0%;
    height: 100%;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    -webkit-animation-duration: 2s;
    animation-duration: 2s;
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

#progress-htmlcss {
    -webkit-animation-name: progress-htmlcss;
    animation-name: progress-htmlcss;
    -webkit-animation-delay: .7s;
    animation-delay: .7s;
}

#progress-jsjq {
    -webkit-animation-name: progress-jsjq;
    animation-name: progress-jsjq;
    -webkit-animation-delay: 1.4s;
    animation-delay: 1.4s;
}

#progress-php {
    -webkit-animation-name: progress-php;
    animation-name: progress-php;
    -webkit-animation-delay: 2.1s;
    animation-delay: 2.1s;
}

#progress-java {
    -webkit-animation-name: progress-java;
    animation-name: progress-java;
    -webkit-animation-delay: .7s;
    animation-delay: .7s;
}

#progress-csharp {
    -webkit-animation-name: progress-csharp;
    animation-name: progress-csharp;
    -webkit-animation-delay: 1.4s;
    animation-delay: 1.4s;
}

#progress-sql {
    -webkit-animation-name: progress-sql;
    animation-name: progress-sql;
    -webkit-animation-delay: 2.1s;
    animation-delay: 2.1s;
}

.progressblue {
    background-color: #70C1B3;
}

@-webkit-keyframes progress-htmlcss {
    0% {
        width: 0%;
    }
    100% {
        width: 90%;
    }
}

@-webkit-keyframes progress-jsjq {
    0% {
        width: 0%;
    }
    100% {
        width: 65%;
    }
}

@-webkit-keyframes progress-php {
